<template>
<div class="main">
    <el-row>
    <el-col :span="5">
        <a href="localhost:8082"  class="aaa">
            <!-- <img width="50px" height="32px" src="@/assets/logo.png"> -->
            xxx学习网站
        </a>
    </el-col>

    <el-col :span="8">
        <el-menu :default-active="1+''" mode="horizontal"  @select="handleSelect" router  active-text-color="black">
            <el-menu-item v-for="(item, index) in headerData"  :key="index" :index="item">
                <template slot="title">{{index}}</template>
            </el-menu-item>
            <el-menu-item index="person" v-show="logined">个人中心</el-menu-item>
            <el-menu-item index="control" v-show="logined_ctrl">管理中心</el-menu-item>
        </el-menu> 
    </el-col>

    <el-col :span="5">
      <el-input placeholder="请输入查询内容" v-model="searchInfo">
        <i slot="suffix" class="el-input__icon el-icon-search"  @click.prevent="search"></i>
      </el-input>
    </el-col>
    <el-col :span="2">
      <br>
    </el-col>
    <el-col  :span="4" v-show="logined" style="padding-left:50px">
      欢迎登录，{{userData.name}} 
      <el-button type="success" @click="exit">注销</el-button>
    </el-col>
    <el-col :span="4" v-show="!logined">
        <a href="http://localhost:8082/#/login">登录</a>|<a href="http://localhost:8082/#/register">注册</a>
    </el-col>

  </el-row>
  </div>
</template>
<script>
export default {
    props:{
        userData:{},
        headerData:{},
        logined: Boolean,
        logined_ctrl:Boolean,
    },
    methods: {
      search(){
        alert(this.userData.name)
      
      },
    handleSelect (key, keyPath) {
      console.log(key, keyPath);
    },
    ReturnTo () {
      this.$router.push({ name: "Login", params: { useId: 1 } })
    },
    handleOpen (key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath);
    },
    refreshUse () {
      this.$router.go(0);
    },
    exitTo () {
      this.$router.push({ name: "Login" })
    },
    exit(){
      this.$router.push({ name: "Login" })
    }
  },
}
</script>
<style scoped>
.main{
  /* background-color:rgb(91, 206, 235); */
}
.aaa{
    text-decoration: none;
} 

</style>


