<template>
<div>
  <div >
      <router-link to='login' style="text-decoration:none">返回主页</router-link>
    </div>

<div class="main">
    
    <div>
        <el-steps :active="active" finish-status="success" align-center>
            <el-step title="步骤 1"></el-step>
            <el-step title="步骤 2"></el-step>
            <el-step title="步骤 3"></el-step>
        </el-steps>
    </div>
   <div v-show="a" style="margin-top:20%;width:40%;margin-left:30%">
    <el-form ref="form" :model="form" label-width="80px" :rules="loginFormRules2">
        <el-form-item label="邮箱" prop="mail">
            <el-input v-model="form.mail"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="yanzhengma">
            <el-input v-model="form.yanzhengma"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit1">下一步</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
    </div>
    <div v-show="b" style="margin-top:20%;width:20%" >
        <el-form ref="form" :model="form" label-width="80px" :rules="loginFormRules1">
        <el-form-item label="用户名" prop="name">
            <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
            <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码">
            <el-input v-model="form.password2" prop="password2"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit2">下一步</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
    </div>
    <div v-show="c" style="margin-top:20%;width:20%">
        <h2>恭喜您，注册成功！</h2>
        <router-link to="login" style="text-decoration:none">登录</router-link>
    </div>
    </div>
</div>
</template>
<script>
  export default {
    data() {
      return {
        a:true,
        b:false,
        c:false,
        active:0,
        form: {
          name: '',
          password:'',
          password2:'',
          mail:'',
          yanzhengma:''
        },
        loginFormRules1: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 20 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
        password2: [
          { required: true, message: "密码确认失败", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
      loginFormRules2: {
        mail: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            min: 3,
            max: 20,
            message: "长度在 3 到 20 个字符",
            trigger: "blur",
          },
        ],
        yanzhengma: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
      }
    },
    methods: {
      onSubmit1() {
          this.a=!this.a
          this.b=!this.b
          this.next()
        console.log('submit!')
        // alert("adasd")
        // alert(''+this.$store.state.test)
      },
      onSubmit2() {
          this.b=!this.b
          this.c=!this.c
          this.next()
        console.log('submit!');
      },
       next() {
        if (this.active++ > 2) this.active = 0;
      }
    }
  }
</script>
<style scoped>
.main{
    width: 60%;
    margin-left: 20%;
    /* background-color: rgb(74, 216, 235); */
}
.el-input{
    width: 300px;
}
</style>


