<template>
<div class="main">
  <el-row>
    <el-container >
      <el-header>
        <home-nav :headerData="headerData" :logined="logined" :logined_ctrl="logined_ctrl" :userData="userData"></home-nav>
      </el-header>
      <!-- 下句是放aside和main布局 -->
      <!-- <keep-alive> -->
      <el-main>
        <router-view></router-view>
      </el-main>
      <!-- </keep-alive> -->
    </el-container>
  </el-row>
</div>
</template>

<style scope>
.main{

}
.el-row{
  width: 100%;
  height: 100%;
}
/* 整个el-header部分 */
.el-header {
    margin: 0%!important;
    width: 100%!important;
    height: 10%!important;
    border-radius: 0px!important;
    position: fixed;
    /* color: black; */
    /* font-size: 16px; */
    /* background-color: rgb(125, 236, 240); */
    
}
.el-main{
  height: 90%;
  width: 100%;
  background-color: rgb(134, 231, 248);
  margin-top:5%
}
/*  此处设置页面最初头部的image */
</style>

<script>
import HomeNav from '@/components/other/home-nav.vue';

export default {
  components:{
    HomeNav
  },
  created(){
    this.userData=this.$store.state.user
    if(this.userData.name==''){
      this.logined=false
      // alert("用户没登录")
    }else{
      this.logined=true
      // alert("欢迎登录！"+"状态："+this.logined)
    }
    if(this.userData.auth=='2'){
      this.logined_ctrl=true
    }
  },
  data () {
    return {
      userData:{},//登录用户的信息
      logined: false, //判断是否登录
      logined_ctrl: false,//判断是否为管理员
      activeIndex: '1',
      activeIndex2: '1',
      useProcess: '20',
      isCollapse: false,
      headerData: {//导航栏菜单
        "首页": "shouye",
        "学习中心": "xuexi",
        "在线论坛":'luntan'
        // "音乐": "music",
      }
    };
  },
  methods:{
    
  }
}
</script>


