import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/home/index'
import Login from '@/components/login/index'
import Register from '@/components/register/index.vue'

import Person from '@/components/home/person.vue'
import Control from '@/components/home/control.vue'
import Xuexi from '@/components/home/xuexi.vue'
// import Music from '@/components/home/music.vue'
import Shouye from '@/components/home/shouye.vue'
import Luntan from '@/components/home/luntan.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect:'/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      // children: [
      //   {
      //     path: '/test',
      //     name: 'Test',
      //     component: Test,
      //   }
      // ]
    },
    {
      path: '/home',
      name :'Home',
      component: Home,
      children: [
        {
          path: '/person',
          name :'Person',
          component: Person,
        },{
          path: '/control',
          name :'Control',
          component: Control,
        },{
          path: '/xuexi',
          name :'Xuexi',
          component: Xuexi
        },{
          path: '/shouye',
          name :'Shouye',
          component: Shouye,
        },
        {
          path: '/luntan',
          name :'Luntan',
          component: Luntan,
        }
      ]
    }
  ]
})



